import React, {useState} from 'react'
import {Link} from 'react-router-dom'

import './styles/authForms.scss'
import {BiEnvelope} from 'react-icons/bi'
import {VscLock} from 'react-icons/vsc'

const LoginForm = () => {

    const [email, setEmail] = useState('');
    const [password, setPassword] = useState("");

    return (
        <div className='form-container'>
            <form action=''>
                <div className='logo'></div>
                <h1 data-aos="fade-right" data-aos-easing="ease-in-sine">Welcome Back</h1>
                <p> 
                    Log into your account or
                    <Link to='/Signup'> Create New Account</Link>
                </p>

                <div className='inner-input-content'>
                    <span><BiEnvelope /></span>
                    <input
                        type='email'
                        name='email'
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                        placeholder='Enter email'
                        className='auth-form-input'
                    />
                </div>

                <div className='inner-input-content'>
                    <span><VscLock /></span>
                    <input
                        type='password'
                        name='password'
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                        placeholder='Enter password'
                        className='auth-form-input'
                    />
                </div>

                <div className='agree-terms-row'>
                    <div className='inner-row'>
                        <input type='checkbox' name='keep-logged' id='keep-logged' />
                        <label htmlFor='keep-logged' className='form-label-checkbox'>
                            Keep me logged in
                        </label>
                    </div>
                    <Link to='/' className='link'>Forgot Password?</Link>
                </div>

                <button type='submit' className='submit-button'>Log In</button>

            </form>
        </div>
    )
}

export default LoginForm
